import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import Register from '../views/Register.vue'
import Login from '../views/Login.vue'
import Index from '../views/Index.vue'
import Home from '../views/Home.vue'



import User from '../views/User.vue'
import ResetPwd from '../views/ResetPwd.vue'
import store from '../store/index'
import Category from '@/views/Category'
import Brand from '@/views/Brand'

import Brandtj from '@/views/Brandtj'
import Handbag from '@/views/Handbag'
import Other from '@/views/Other'
import Ornament from '@/views/Ornament'
import Shoe from '@/views/Shoe'
import Meart from '@/views/Meart'
import Xqy from '@/views/Xqy'
import Cart from '@/views/Cart'
import Order from '@/views/Order'
import Payment from '@/views/Payment'


//马文庆
import Dingdan from '../views/Dingdan.vue'
import Usermsg from '../views/Usermsg.vue'
import Address from '../views/Address.vue'
import Collect from '../views/Collect.vue'
//马文庆

//周国强
import Addshop from '../views/Addshop.vue'
import Showshop from '../views/Showshop.vue'
//周国强


import {
    Search
} from 'vant';
import {
    TreeSelect
} from 'vant';
import {
    Swipe,
    SwipeItem
} from 'vant';
import {
    Row,
    Col
} from 'vant';
import {
    Divider
} from 'vant';
import {
    Grid,
    GridItem
} from 'vant';
import {
    DropdownMenu,
    DropdownItem
} from 'vant';
import {
    IndexBar,
    IndexAnchor
} from 'vant';
import {
    Button
} from 'vant';
import {
    SubmitBar
} from 'vant';
import {
    NavBar
} from 'vant';
import {
    Icon
} from 'vant';
import {
    GoodsAction,
    GoodsActionIcon,
    GoodsActionButton
} from 'vant';
import {
    CountDown
} from 'vant';
import {
    SwitchCell
} from 'vant';
import {
    ContactCard,
    ContactList,
    ContactEdit
} from 'vant';
import {
    Card
} from 'vant';
import {
    CouponCell,
    CouponList
} from 'vant';
import {
    Field
} from 'vant';
import {
    RadioGroup,
    Radio
} from 'vant';
import {
    Image
} from 'vant';
Vue.use(Image);
Vue.use(RadioGroup);
Vue.use(Radio);

Vue.use(CouponCell).use(CouponList);
Vue.use(SwitchCell);
Vue.use(Field);
Vue.use(Card);
Vue
    .use(ContactCard)
    .use(ContactList)
    .use(ContactEdit);
Vue.use(CountDown);
Vue
    .use(GoodsAction)
    .use(GoodsActionIcon)
    .use(GoodsActionButton);
Vue.use(Icon);
Vue.use(NavBar);
Vue.use(Button);
Vue.use(IndexBar).use(IndexAnchor);
Vue.use(DropdownMenu).use(DropdownItem);
Vue.use(Grid).use(GridItem);
Vue.use(Row).use(Col);
Vue.use(Router)
Vue.use(Search);
Vue.use(TreeSelect);
Vue.use(Swipe).use(SwipeItem);
Vue.use(Divider);
Vue.use(SubmitBar);



//马文庆
import { NoticeBar } from 'vant';
Vue.use(NoticeBar);
import { AddressList } from 'vant';
Vue.use(AddressList);
import { Tab, Tabs } from 'vant';
Vue.use(Tab).use(Tabs);
import { Popup } from 'vant';
Vue.use(Popup);
import { Panel } from 'vant';
Vue.use(Panel);
import { Uploader } from 'vant';
Vue.use(Uploader);
//马文庆












export default new Router({
    routes: [{
            path: '/',
            redirect: '/login'
        },
        {
            path: '/login',
            component: Login
        },
        {
            path: '/register',
            component: Register
        },
        {
            path: '/resetPwd',
            component: ResetPwd
        },
        {
            path: '/index',
            redirect: '/home',
            name: 'Index ',

            component: Index,
            children: [
                { path: '/home', component: Home },
                {
                    path: '/category',
                    component: Category,
                    children: [{
                            path: '/brand',
                            component: Brand,
                        }, {
                            path: '/handbag',
                            component: Handbag,
                        }, {
                            path: '/other',
                            component: Other,
                        },
                        {
                            path: '/ornament',
                            component: Ornament,
                        },
                        {
                            path: '/meart',
                            component: Meart,
                        },
                        {
                            path: '/shoe',
                            component: Shoe,
                        }
                    ]
                },
                { path: '/cart', component: Cart },
                { path: '/user', component: User },
            ],
        },
        {
            path: '/brandtj',
            component: Brandtj,
        },
        {
            path: '/xqy/:id',
            name: 'Xqy',
            component: Xqy,
        },
        {
            path: '/order',
            name: 'Order',
            component: Order
        },
        {
            path: '/payment',
            name: 'Payment',
            component: Payment
        },

        //马文庆
        {
            path: '/dingdan',
            component: Dingdan
        },
        {
            path: "/usermsg",
            component: Usermsg
        },
        {
            path: "/address",
            component: Address
        },
        {
            path: "/collect",
            component: Collect
        },
        //马文庆

        //周国强
        {
            path: "/showshop",
            component: Showshop
        },
        {
            path: "/addshop",
            component: Addshop
        }
        //周国强




    ]
})


