<template>
  <div id="my">
    <div class="head">


<van-row style="padding-top:30px">
  <van-col span="8">
    
      <van-image
        @click="click()"
        :round="true"
        class="touxiang"
        width="80px"
        height="80px"
        :src="imgsrc"
      />

  </van-col>
  <van-col span="8">
    
      <p class="name">{{name}}</p>
      <p class="phone">{{phone}}</p>

  </van-col>
  <van-col span="8">
    
      <div id="weather">
          <p>{{wea}}</p>
          <p>{{day}}</p>
          <p>{{wea}}</p>
          <p>>{{tem}}</p>
          <p>{{air_level}}</p>
      </div>

  </van-col>
</van-row>

      <van-row class="row"  style="padding-top:10px">
        <van-col span="12">{{money}}</van-col>
        <van-col span="12">{{zhekou}}</van-col>
      </van-row>
      <van-row class="row row1">
        <van-col span="12">余额</van-col>
        <van-col span="12">优惠券</van-col>
      </van-row>


<van-row style="margin-top:30px">
  <van-col span="24">
    <van-notice-bar
        class="gonggao"
        :text="'订阅上新  欢迎用户'+text+'回家    欢迎用户'+text+'回家       欢迎用户'+text+'回家'"
        left-icon="volume-o"
        mode="closeable"
      />
  </van-col>
</van-row>


    </div>
    <div style="margin-top:50px">
      <div>
        <van-button @click="tap1()" plain type="primary">我是买家</van-button>
        <van-button @click="tap2()" plain type="primary">我是卖家</van-button>
      </div>
      <div id="box1" style="display:initial">
        <van-grid>
          <van-grid-item icon="coupon-o" text="全部订单" to="/dingdan" />
          <van-grid-item icon="balance-o" text="待付款" to="/dingdan" />
          <van-grid-item icon="logistics" text="待收货" to="/dingdan" />
          <van-grid-item icon="passed" text="已完成" to="/dingdan" />
        </van-grid>
      </div>
      <div id="box2" style="display:none">
        <van-grid :column-num="2">
          <van-grid-item icon="add-o" text="我要寄卖" @click="toaddshop()" />
          <van-grid-item icon="exchange" text="我要转卖" @click="toshowshop()" />
        </van-grid>
      </div>
      <div>
        <p style="margin: 5px 0">我的服务</p>
        <van-grid :column-num="4">
          <van-grid-item icon="like-o" text="收藏" to="/collect" />
          <van-grid-item icon="gift-o" text="捡漏专区" to="/collect" />

          <van-grid-item icon="location-o" text="地址管理" @click="address()" />
          <van-grid-item icon="setting-o" text="设置" @click="shezhi" />
        </van-grid>
      </div>
      <div>
        <p style="margin: 5px 0">猜你喜欢商品名称</p>

        <div class="likefather">
          <div @click="godetail(i-1+2)" class="like left" v-for="(item,i) in like" :key="i">
            <img :src="item.path" alt />
            <p>{{item.goodsName}}</p>
            <span>￥{{item.money}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "User",
  data() {
    return {
      imgsrc: "https://img.yzcdn.cn/vant/cat.jpeg",
      name: "这是id展示区域",
      money: "***",
      zhekou: "0",
      text: "",
      like: [],
      phone: "",
      id: "13",
      day: "",
      tem: "",
      wea: "",
      air_level: "",
      imgsrc1: ""
    };
  },
  methods: {
    click() {
      //点击头像修改个人信息，
      this.$router.push("/usermsg");
    },
    tap1() {
      box1.style.display = "initial";
      box2.style.display = "none";
    },
    tap2() {
      box1.style.display = "none";
      box2.style.display = "initial";
    },
    address() {
      this.$router.push("/address");
    },
    godetail(id) {
      //console.log("点击跳转到详情页")
      this.$router.push("/xqy/id=" + id);
    },
    shezhi() {
      this.$toast("该功能暂未开放");
    },
    //此处应写在登录界面，在登录界面传递给我 id
    // sendid(){
    //   this.$store.commit('sendid',this.id)
    // }
    //此处 写在接收界面
    //this.$store.state.id
    toaddshop() {
      this.$router.push("/addshop");
    },
    toshowshop() {
      this.$router.push("/showshop");
    }
  },
  filters: {
    info: function(data) {
      switch (data) {
        case "晴天":
          return "https://i01piccdn.sogoucdn.com/e42e8ca25bfe55dd";
        case "多云":
          return "https://i04piccdn.sogoucdn.com/9b16eb9ebe443c20";
        case "雨天":
          return "https://pic.90sjimg.com/design/01/30/13/50/5a3a5966c999a.png";
      }
    }
  },
  mounted() {
    var _this = this;

    var aid = this.$store.state.userID;

    axios({
      //此处为用户头像展示
      method: "post",
      url: "http://106.12.14.214:8889/luxury/admin/message.do",
      params: {
        id: aid
      }
    }).then(data => {
      //console.log(data)
      _this.imgsrc = data.data.userImg;
      _this.name = data.data.loginName;
      _this.text = data.data.loginName;
      _this.phone = data.data.phone;
    }),
      //此处为猜你喜欢页面
      axios({
        method: "post",
        url: "http://106.12.14.214:8889/luxury/goods/listShow",
        params: {
          flag: 0
        }
      }).then(data => {
        //console.log(data.data)
        _this.like = data.data;
      }),
      //此处是天气展示
      axios({
        url: "https://www.tianqiapi.com/api/?version=v1",
        params: {
          city: "郑州"
        }
      }).then(data => {
        //console.log(data.data.data)
        _this.day = data.data.data[0].day;
        _this.tem = data.data.data[0].tem;
        _this.wea = data.data.data[0].wea;
        _this.air_level = data.data.data[0].air_level;
      });
  }
};
</script>
<style scope>
* {
  margin: 0;
  padding: 0;
}
#app {
  margin-bottom: 50px;
}
.none {
  display: none;
}
.left {
  float: left;
}
.head {
  background: #f00;
  /* background-image: url(https://img04.sogoucdn.com/net/a/04/link?url=https%3A%2F%2Fi04piccdn.sogoucdn.com%2F4ce494e552240d4c&appid=122); */
  height: 190px;
}
#weather {
  
  width: 80px;
  height: 70px;
  
  font-size: 12px;
}
.touxiang {
  border-radius: 50%;
  overflow: hidden;
}
.name {
 
}
.phone {
  
}
.row {
  
}
.row1 {
  
}
.gonggao {
  
}
.likefather {
  overflow: hidden;
  height: auto;
}
.like {
  width: 48%;
  height: 300px;
  border: 1px solid #ccc;
  margin: 2px;
}
.like img {
  width: 98%;
  height: 250px;
}
</style>



