<template>
<div>
<van-row>
  <van-nav-bar
            title="寄卖"
            left-text="返回"
            left-arrow
            right-text=""
            @click-left="abcdback"
        />

</van-nav-bar>
</van-row>

  <br/>
  <br/>
<van-row name="">
<h4>商品属性</h4>
  <van-col span=""><van-checkbox v-model="checked">男士</van-checkbox></van-col>
  <van-col span=""><van-checkbox v-model="checked">女士</van-checkbox></van-col>
</van-row>
<van-row>
  <h4>品类</h4><br/>
  <van-col span=""><van-checkbox v-model="checked">包袋</van-checkbox></van-col>
  <van-col span=""><van-checkbox v-model="checked">配饰</van-checkbox></van-col>
  <van-col span=""><van-checkbox v-model="checked">服饰</van-checkbox></van-col>
  <van-col span=""><van-checkbox v-model="checked">鞋靴</van-checkbox></van-col>
</van-row>
<van-row><h4>品牌</h4></van-row>
<van-dropdown-menu>
  <van-dropdown-item v-model="value1" :options="option1" />
</van-dropdown-menu>
<van-row>
<h4>商品图片</h4>
</van-row>
<van-uploader :before-read="beforeRead" />
<van-uploader :before-read="beforeRead" />
<van-uploader :before-read="beforeRead" />
<van-uploader :before-read="beforeRead" />
<van-uploader :before-read="beforeRead" />
<van-uploader :before-read="beforeRead" />
<van-uploader :before-read="beforeRead" />
<van-uploader :before-read="beforeRead" />

<van-cell-group>
  <van-field
    v-model="message"
    label="补充（选填）"
    type="textarea"
    placeholder="可提高鉴别与发布商品"
    rows="3"
    autosize
  />
</van-cell-group>

<van-tabbar
  v-model="active"
  active-color="#07c160"
  inactive-color="#000"
>
  <van-button round type="danger" size="large">提交</van-button>
</van-tabbar>

<!-- 下拉刷新 -->
<van-pull-refresh v-model="isLoading" @refresh="onRefresh">
  <p>刷新成功 {{ count }}</p>
</van-pull-refresh>

<router-view></router-view>
</div>
</template>

<script>
export default {
  name:"Addshop",
   data() {
    return {
      checked: true,
      radio: '2',
// 下拉菜单
      value1: 1,
      option1: [
        { text: '全部分类', value: 0 },
        { text: '新款商品', value: 1 },
        { text: '活动商品', value: 2 }
      ],
      // 下拉刷新
      count: 0,
      isLoading: false,

      fileList: [
        { url: 'https://img.yzcdn.cn/vant/cat.jpeg' }
      ]
    }
  },

methods: {
  //返回按钮
    onClickLeft() {
      Toast('返回');
    },
    abcdback(){
      this.$router.go(-1)
    }
},
// 下拉刷新
onRefresh() {
      setTimeout(() => {
        this.$toast('刷新成功');
        this.isLoading = false;
        this.count++;
      }, 500);
  },
// 返回布尔值
    beforeRead(file) {
      if (file.type !== 'image/jpeg') {
        Toast('请上传 jpg 格式图片');
        return false;
      }

      return true;
    },

    // 返回 Promise
    asyncBeforeRead(file) {
      return new Promise((resolve, reject) => {
        if (file.type !== 'image/jpeg') {
          Toast('请上传 jpg 格式图片');
          reject();
        } else {
          resolve();
        }
      });
    }
}
</script>

<style>
/*应用图标 */
/* @font-face {
  font-family: 'my-icon';
  src: url('./icon/iconfont.ttf') format('truetype');
}

.my-icon {
  font-family: 'my-icon';
}

.my-icon-extra::before {
  content: '\e626';
}*/
</style>



